/* Global Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Roboto', Arial, sans-serif;
  background-color: #f7f8fb;  /* Soft background color */
  color: #333;
  line-height: 1.6;
  padding: 0;
  font-size: 16px;
}

h1, h2, h3 {
  color: #333;
  font-weight: 700;
}

a {
  text-decoration: none;
  color: #3498db; /* Primary link color */
}

a:hover {
  text-decoration: underline;
}

/* Navbar */
nav {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 15px 30px;
  background-color: #fff;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

nav .logo {
  display: flex;
  justify-content: center;
  align-items: center;
}

nav .logo img {
  max-width: 120px; /* Adjust the logo size */
  height: auto;
}

nav ul {
  display: flex;
  justify-content: space-around;
  list-style-type: none;
}

nav ul li {
  margin: 0 15px;
}

nav ul li a {
  color: #3498db;
  font-weight: 600;
  font-size: 18px;
  transition: color 0.3s ease;
}

nav ul li a:hover {
  color: #e74c3c; /* Accent color */
}

/* Form Styles */
form {
  background-color: #fff;
  padding: 35px;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
  width: 100%;
  max-width: 450px;
  margin: 50px auto;
}

form div {
  margin-bottom: 20px;
}

form label {
  display: block;
  font-weight: 600;
  margin-bottom: 8px;
  color: #333;
}

form input {
  width: 100%;
  padding: 12px;
  border: 1px solid #ddd;
  border-radius: 6px;
  font-size: 16px;
  transition: border-color 0.3s ease;
}

form input:focus {
  border-color: #3498db;
  outline: none;
}

form button {
  width: 100%;
  padding: 14px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 6px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

form button:hover {
  background-color: #2980b9;
}

form button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* Error Messages */
.error-message {
  color: #e74c3c;
  margin-bottom: 20px;
}

/* Profile Page */
.profile-container {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  padding: 40px;
  background-color: #fff;
  border-radius: 8px;
  box-shadow: 0 10px 20px rgba(0, 0, 0, 0.1);
}

.profile-container h1 {
  font-size: 2rem;
  color: #3498db;
}

.profile-container button {
  background-color: #e74c3c;
  color: white;
  border-radius: 6px;
  padding: 12px 24px;
  border: none;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

.profile-container button:hover {
  background-color: #c0392b;
}

/* About Us Page */
.about-us {
  text-align: center;
  padding: 60px 20px;
  background-color: #fff;
}

.about-us h1 {
  font-size: 2.5rem;
  color: #3498db;
  font-weight: 700;
  margin-bottom: 20px;
}

.about-us p {
  font-size: 1.125rem;
  color: #555;
  line-height: 1.8;
  margin-bottom: 30px;
}

.about-us button {
  background-color: #3498db;
  color: white;
  padding: 12px 24px;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  font-size: 16px;
  transition: background-color 0.3s ease;
}

.about-us button:hover {
  background-color: #2980b9;
}

/* Responsive Design */
@media (max-width: 768px) {
  nav {
    flex-direction: column;
    padding: 20px 15px;
  }

  nav ul {
    flex-direction: column;
    align-items: center;
    margin-top: 20px;
  }

  nav ul li {
    margin: 10px 0;
  }

  .container {
    padding: 20px;
  }

  .profile-container {
    padding: 30px;
    width: 90%;
  }

  .about-us {
    padding: 40px 15px;
  }
}

.placement-test {
  margin-top: 20px;
  text-align: center;
}

button {
  padding: 12px 25px;
  background-color: #3498db;
  color: white;
  border: none;
  border-radius: 6px;
  cursor: pointer;
  transition: background-color 0.3s ease;
}

button:hover {
  background-color: #2980b9;
}

/* Custom Buttons (for Placement Test and other actions) */
button:disabled {
  background-color: #ddd;
  cursor: not-allowed;
}

/* Progress Bar */
.progress-bar {
  width: 100%;
  background-color: #ddd;
  border-radius: 6px;
  height: 8px;
  margin-top: 20px;
}

.progress-bar-filled {
  height: 100%;
  background-color: #3498db;
  border-radius: 6px;
  transition: width 0.3s ease;
}
